
import Vue from 'vue'
import { cart, pages } from '@/store'
export default Vue.extend({
  computed: {
    page() {
      return pages.$page
    },
  },
  data() {
    return {
      list: {},
    }
  },
  async created() {
    const respon = await cart.getLogs()
    this.list = respon
  },
})
